export function getClubEvents(setEvents) {
  fetch(
    'https://api.apify.com/v2/datasets/PdumrJ2iWNSc8HCcY/items?format=json&clean=1',
  )
    .then(res => res.json())
    .then(out =>
      out.map(data => {
        setEvents(events => ({
          ...events,
          [data.club]: data,
        }));
      }),
    );
}
